import React from 'react';
import Page from 'src/components/Layout/Page';
import { Container, makeStyles, Button } from '@material-ui/core';
import Lottie from 'react-lottie';
import { useSpring, animated, config } from 'react-spring';
import confetti from 'src/assets/lottie/confetti.json';
import Emoji from 'components/Utils/Emoji';

const useStyles = makeStyles(theme => ({
  confetti: {
    position: 'absolute',
    width: '100%',
    top: '50%',
    left: '50%',
    transform: 'translate(-50%, -50%)',
    padding: `0 ${theme.spacing(1)}px`,
    '& > div': {
      [theme.breakpoints.up('lg')]: {
        width: '50% !important',
        height: '50% !important',
      },
    },
  },
  word: {
    margin: '30vh 0',
    textAlign: 'center',
  },
  buttonWrapper: {
    marginTop: theme.spacing(5),
  },
  button: {
    '& + &': {
      marginLeft: theme.spacing(1),
    },
    [theme.breakpoints.down('md')]: {
      width: '100%',
      '& + &': {
        marginLeft: 0,
        marginTop: theme.spacing(1),
      },
    },
  },
}));

const ApplyResult = () => {
  const styles = useStyles();
  const textAnimationOptions = {
    from: { opacity: 0, transform: 'translateY(30px)' },
    to: { opacity: 1, transform: 'translateY(0)' },
    delay: 500,
    config: {
      ...config.wobbly,
    },
  };
  const titleAnimation = useSpring(textAnimationOptions);
  const descriptionAnimation = useSpring({
    ...textAnimationOptions,
    delay: 1000,
  });
  const buttonAnimation = useSpring({
    from: { opacity: 0 },
    to: { opacity: 1 },
    config: {
      duration: 1000,
    },
    delay: 1200,
  });

  return (
    <Page pageName="   ">
      <Container>
        <div className={styles.confetti}>
          <Lottie
            options={{
              animationData: confetti,
              loop: false,
              autoplay: true,
            }}
          />
        </div>

        <div className={styles.word}>
          <animated.h1 style={titleAnimation}>   !</animated.h1>
          <animated.p style={descriptionAnimation}>
                2~3    ! <Emoji emoji="" name="thanks" />
          </animated.p>
          <animated.div style={buttonAnimation} className={styles.buttonWrapper}>
            <Button
              color="default"
              size="large"
              href="/"
              variant="contained"
              className={styles.button}
              disableElevation
            >
              
            </Button>
            <Button
              color="primary"
              size="large"
              href="/magazines"
              variant="contained"
              className={styles.button}
              disableElevation
            >
                 !
            </Button>
          </animated.div>
        </div>
      </Container>
    </Page>
  );
};

export default ApplyResult;
